import React from 'react';
import md5 from 'md5';
import './style.scss';

import { bindActionCreators } from 'redux';
import PropTypes from 'prop-types';
import Button from '../../components/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import Paper from '@material-ui/core/Paper';
import { FormHelperText } from '@material-ui/core';
import Typography from '@material-ui/core/Typography';
import withStyles from '@material-ui/core/styles/withStyles';
import NativeSelect from '@material-ui/core/NativeSelect';
import { connect } from 'react-redux';
import { loginAction } from './action';
import { numberRegex, emailRegex } from '../../config/regex';
import { generateRandomNumber } from '../../utilities/generateRandomNumber';

const styles = theme => ({
    main: {
        width: 'auto',
        display: 'block',
        paddingTop: theme.spacing.unit * 5,
        [theme.breakpoints.up(400 + theme.spacing.unit * 3 * 2)]: {
            width: 500,
            marginLeft: 'auto',
            marginRight: 'auto',
        },
    },
    paper: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        padding: `${theme.spacing.unit * 2}px ${theme.spacing.unit * 3}px ${theme.spacing.unit * 3.5}px`,
    },
    form: {
        width: '100%', height: '100%',
        marginTop: theme.spacing.unit,
    },
    text: {
        fontSize: '2em',
        fontWeight: 400
    },
    selectType: {
        padding: '4px',
        color: 'black',
        opacity: '.75',
        width: '125px',
        marginLeft: '10px',
        paddingLeft: '10px !important'
    },
    loginType: {
        fontWeight: 500,
        marginRight: '8px',
        color: 'black',
        opacity: '.57'
    }
});

class Login extends React.Component {
    state = {
        loginAS: '',
        errors: {},
        fields: {
            username: '',
            password: '',
            role: 'cet'
        },
        num1: 0,
        num2: 0,
        sum: "",
        enableSubmit: false,
    }

    storeSum = (event) => {
        // let numberRegex = /^[0-9]*$/;
        let { sum } = this.state;
        this.setState({ sum: numberRegex.test(event.target.value) ? event.target.value : sum }, () => this.checkValidityOfForm());
    }

    checkValidityOfForm = () => {
        let { fields, errors } = this.state;
        let hasErrors = false, validForm = false;
        let requiredFields = ["username", "password", "role"];
        validForm = requiredFields.every(field => fields[field] !== "");
        hasErrors = Object.keys(errors).length !== 0 && requiredFields.some(field => errors[field] && errors[field] !== "");
        this.setState({
            enableSubmit:
                !hasErrors &&
                validForm &&
                Number(this.state.num1) + Number(this.state.num2) === Number(this.state.sum)
        });
    }

    handleInputChange = (event) => {
        // let emailRegex = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)+$/;
        let fieldName = event.target.name;
        let fieldValue = event.target.value;
        let { fields, errors } = this.state;

        switch (fieldName) {
            case 'username':
                if (!fieldValue) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = "Email is Required!";
                } else if (emailRegex.test(fieldValue)) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = "";
                } else if (!emailRegex.test(fieldValue)) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = "Invalid Email Id!";
                }
                break;

            case 'password':
                if (!fieldValue) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = "Password is Required!";
                } else {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = "";
                }
                break;

            default: fields[fieldName] = fieldValue;
                break;
        }
        this.setState({ fields, errors }, () => {
            this.checkValidityOfForm();
        });
    }
    componentDidMount() {
        this.setState({ num1: generateRandomNumber(1, 9), num2: generateRandomNumber(1, 9) });
    }

    onSubmit = (event) => {
        event.preventDefault();
        if (this.state.num1 + this.state.num2 === Number(this.state.sum)) {
            this.props.loginAction({ ...this.state.fields, password: md5(this.state.fields.password) }, this.props);
        }
    }

    render() {
        let { classes, userDetails, loginError } = this.props;
        const { fields, errors } = this.state;

        if (Object.keys(userDetails).length === 0 && !localStorage.getItem('authToken')) {
            return (
                <main className={classes.main}>
                    <CssBaseline />
                    <Paper className={classes.paper}>
                        <Typography component="h1" variant="h5" className={classes.text}>
                            BACKOFFICE
                        </Typography>
                        <form className={classes.form}>
                            {loginError && <div className="login_Error">{loginError}</div>}
                            <div>
                                <FormControl margin="normal" className="login-main" required fullWidth error={errors.username}>
                                    <InputLabel htmlFor="email" className="login-label">Email Address</InputLabel>
                                    <Input id="email"
                                        type="email"
                                        name="username"
                                        autoComplete="email"
                                        autoFocus
                                        value={fields.username}
                                        onChange={this.handleInputChange} />
                                    <FormHelperText className="error-text" id="email-text">{errors.username}</FormHelperText>
                                </FormControl>
                            </div>

                            <div>
                                <FormControl margin="normal" className="login-main" required fullWidth error={errors.password}>
                                    <InputLabel htmlFor="password" className="login-label">Password</InputLabel>
                                    <Input
                                        name="password"
                                        type="password"
                                        id="password"
                                        autoComplete="current-password"
                                        value={fields.password}
                                        onChange={this.handleInputChange} />
                                    <FormHelperText className="error-text" id="paasword-text">{errors.password}</FormHelperText>
                                </FormControl>
                            </div>

                            <div className="LOGINType">
                                <InputLabel htmlFor="age-nloginErrorative-label-placeholder" className={classes.loginType}>
                                    Role
                                <span className="MuiFormLabel-asterisk-92"> *</span>
                                </InputLabel>
                                <NativeSelect
                                    value={fields.role}
                                    className={classes.selectType}
                                    name="role"
                                    onChange={this.handleInputChange}
                                    input={<Input name="loginAS" id="age-native-label-placeholder" />}
                                >
                                    <option value="admin">Admin</option>
                                    <option value="cet">CET</option>
                                    <option value="coupon manager">Coupon Manager</option>
                                    <option value="finance">Finance</option>
                                    <option value="operations">Operations</option>
                                    <option value="partnerships">Partnership</option>
                                    <option value="sales">Sales</option>
                                    <option value="team leader">Team Leader</option>
                                </NativeSelect>
                            </div>
                            <div className="summation-box">
                                <div className="numbers">{this.state.num1}</div>
                                <div className="symbol">+</div>
                                <div className="numbers">{this.state.num2}</div>
                                <div className="symbol">=</div>
                                <input className="sum-input" maxLength="2" onChange={this.storeSum} value={this.state.sum} placeholder="Result" />
                            </div>
                            <div className="login-btn-container">
                                <Button
                                    disabled={!this.state.enableSubmit}
                                    onClick={this.onSubmit}
                                    label="Log in"
                                />
                            </div>
                        </form>
                    </Paper>
                </main>
            );
        } else if (localStorage.getItem('authToken') && userDetails && userDetails.role) {
            if (userDetails && userDetails.role && userDetails.role.includes("TEAML"))
                this.props.history.push("/order-search");
            else if (userDetails && userDetails.role && userDetails.role.includes("OC"))
                this.props.history.push("order-confirmation-team");
            else if (userDetails && userDetails.role && userDetails.role.includes("RT"))
                this.props.history.push("relationship-team");
            else if (userDetails && userDetails.role && userDetails.role.includes("C"))
                this.props.history.push("returns-replacement");
            else if (userDetails && userDetails.role && userDetails.role.includes("OPS"))
                this.props.history.push("import-orders");
            else if (userDetails && userDetails.role && userDetails.role.includes("SALES") && userDetails.is_pod_leader === 1)
                this.props.history.push("/sales-team");
            else if (userDetails && userDetails.role && userDetails.role.includes("SALES"))
                this.props.history.push("/order-search");
            else if (userDetails && userDetails.role && userDetails.role.includes("FINANCE"))
                this.props.history.push("/refund-list");
            else
                this.props.history.push("/");
            return '';
        } else {
            this.props.history.push("/");
            return "";
        }
    }
}

Login.propTypes = {
    classes: PropTypes.object.isRequired,
};


const mapStateToProps = (state, ownProps) => {
    return {
        userDetails: state.userDetails,
        loginError: state.login.error
    }
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({ loginAction }, dispatch)
}

export default withStyles(styles)(connect(mapStateToProps, mapDispatchToProps)(Login));
